.signature {
	margin: 0;
	padding-top: 32px;
	text-align: center;
	background-image: url("../images/pattern-bar.png");
	background-size: cover;
	background-repeat: no-repeat;

	.desktop-show {
		display: none;
	}

	&__text-block {
		padding-bottom: 25px;
	}

	&__list-star {
		padding-top: 33px;
	}

	h5 {
		margin: 0 0 16px;
		font-weight: 600;
		font-size: 30px;
		color: #4e2f6b;
	}

	img {
		width: 220px;
		max-width: 100%;
	}

	p {
		margin-bottom: 10px;
		font-weight: 400;
		font-size: 13px;
		color: #483269;
	}

	strong {
		display: block;
		margin-bottom: 8px;
		padding: 0 37px;
		font-weight: 600;
		font-size: 16px;
		text-align: center;
	}

	&__list-star {
		display: flex;
		flex-wrap: wrap;
		list-style: none;
		padding: 0;
	}

	&__star-item {
		padding: 0 20px;
		width: 50%;
		font-weight: 500;

		span {
			display: block;
			font-weight: 600;
			font-size: 26px;
		}

		p {
			color: #483269;
		}

		svg {
			width: 68px;
			height: 68px;
		}
	}

	@include tablet {
		padding-top: 57px;
		padding-bottom: 41px;

		h5 {
			font-size: 60px;
		}

		img {
			width: 460px;
		}

		&__text-block {
			margin-top: -28px;
		}

		p {
			margin-bottom: 18px;
			font-size: 25px;
			line-height: 35px;
		}

		strong {
			margin: 0 auto 15px;
			max-width: 600px;
			font-size: 31px;
		}
	}

	@include desktop {
		&__content {
			display: flex;
			align-items: flex-start;
		}

		.desktop-show {
			display: block;
		}

		.desktop-none {
			display: none;
		}

		&__text-block {
			text-align: left;
		}

		strong {
			margin: 0 0 15px;
			padding: 0;
			text-align: left;
		}
	}

	@include desktop-lg {
		&__content {
			padding-top: 60px;
		}

		img {
			width: 668px;
		}

		&__text-block {
			padding-top: 103px;
			max-width: 630px;

			h5 {
				font-size: 80px;
			}

			p {
				margin-bottom: 26px;
				font-size: 20px;
				line-height: 30px;
			}

			strong {
				margin-bottom: 2px;
				font-size: 20px;
			}
		}
	}
}
