.slider-signature {
	&__slider-wrapper {
		position: relative;
		margin: 0 auto;
		width: 230px;
	}

	&__show-info {
		display: none;
		transition: 0.2s linear;
	}

	h3 {
		margin: 0 auto;
		margin-bottom: 14px;
		max-width: 260px;
		font-weight: 600;
		font-size: 30px;
		line-height: 32px;
		text-align: center;
	}

	p {
		font-size: 13px;
		line-height: 17px;
		text-align: center;
		color: #483269;
	}

	img {
		margin-bottom: 15px;
		width: 100%;
		height: 235px;
		object-fit: cover;
	}

	&__info {
		overflow: hidden;
		margin: 0 auto;
		width: 230px;
	}

	.swiper-button-signature-prev,
	.swiper-button-signature-next {
		position: absolute;
		top: 130px;
		width: 32px;
		height: 32px;

		svg {
			width: 100%;
			height: 100%;
		}
	}

	.swiper-button-signature-prev {
		left: -50px;
	}

	.swiper-button-signature-next {
		right: -50px;
		transform: rotate(180deg);
	}

	span {
		display: block;
		margin-bottom: 10px;
		font-weight: 600;
		font-size: 19px;
		text-align: center;
	}

	@include tablet {
		h3 {
			margin-bottom: 53px;
			max-width: 475px;
			font-size: 52px;
			line-height: 55px;
		}

		&__slider-wrapper {
			width: 470px;
		}

		img {
			margin-bottom: 40px;
			height: 462px;
		}

		&__info {
			width: 465px;
		}

		.swiper-button-signature-prev,
		.swiper-button-signature-next {
			top: 35%;
			width: 58px;
			height: 58px;
		}

		.swiper-button-signature-prev {
			left: -93px;
		}

		.swiper-button-signature-next {
			right: -93px;
		}

		span {
			font-size: 39px;
		}

		p {
			font-size: 26px;
			line-height: 32px;
		}
	}

	@include desktop {
		.swiper-button-signature-prev,
		.swiper-button-signature-next {
			display: none;
		}

		span {
			color: #fff;
		}

		img {
			margin: 0;
			height: 366px;
		}

		p {
			color: #fff;
		}

		h3 {
			max-width: initial;
		}

		&__info {
			width: auto;
		}

		&__slider-wrapper {
			width: 100%;
		}

		&__content {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			justify-content: flex-start;
		}

		.swiper-slide__text {
			position: absolute;
			top: 0;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			padding: 0 24px;
			width: 100%;
			height: 366px;
			background-color: rgba(103, 76, 129, 0.8);
			opacity: 0;
			transition: 0.2s linear;

			p {
				display: -webkit-box;
				overflow: hidden;
				width: 100%;
				height: 144px;
				font-size: 20px;
				line-height: 24px;
				text-overflow: ellipsis;
				-webkit-box-orient: vertical;
				-webkit-line-clamp: 6;
			}
		}

		.swiper-slide {
			margin: 0 16px 32px;
			width: calc(33% - 30px);

			&:hover {
				.swiper-slide__text {
					opacity: 1;
				}

				.slider-signature__show-info {
					opacity: 0;
				}
			}
		}

		p.slider-signature__show-info {
			position: absolute;
			left: 27px;
			bottom: 27px;
			display: block;
			padding: 2px 6px;
			font-weight: 600;
			font-size: 28px;
			color: #4e2f6b;
			background: rgba(255, 255, 255, 0.8);
		}
	}

	@include desktop-lg {
		margin: 0;
		padding-bottom: 45px;

		h3 {
			margin-bottom: 83px;
			font-size: 65px;
		}

		&__content {
			margin: 0 auto;
			max-width: 1280px;
		}

		img {
			height: 390px;
		}

		.swiper-slide__text {
			justify-content: flex-start;
			padding-top: 95px;
			height: 390px;

			span {
				margin-bottom: 25px;
				font-size: 30px;
			}

			p {
				overflow: hidden;
				height: 140px;
				font-size: 22px;
				line-height: 28px;

				&.slider-signature__show-info {
					height: auto;
				}
			}
		}
	}
}
