.footer {
	position: relative;
	margin-top: -1px;
	padding: 37px 0 24px;
	text-align: center;
	background-color: #462e69;

	p {
		margin-bottom: 23px;
		font-size: 9px;
		color: #fff;
	}

	&__logo {
		display: flex;
		margin: 0 auto;
		width: 84px;

		svg {
			height: 40px;
			fill: #fff;
		}
	}

	&__anchor {
		position: absolute;
		left: 50%;
		right: 0;
		top: -14px;
		width: 30px;
		height: 30px;
		transform: translate(-50%, 0);

		svg {
			width: 100%;
			height: 100%;
		}
	}

	@include tablet {
		padding: 78px 0 60px;

		&__anchor {
			right: 0;
			top: -27px;
			width: 60px;
			height: 60px;
		}

		p {
			margin-bottom: 43px;
			font-size: 17px;
		}

		&__logo {
			width: 200px;

			svg {
				height: 65px;
			}
		}
	}
}
