.sign-info {
	margin: 0;

	&__wrap-list {
		margin-bottom: 12px;
		background: #fff;
	}

	h2 {
		margin: 0 auto;
		max-width: 290px;
		font-size: 30px;
		text-align: center;
	}

	&__list-star {
		display: flex;
		flex-wrap: wrap;
		list-style: none;
		padding: 0;
		padding-top: 32px;
	}

	&__star-item {
		margin-bottom: 16px;
		padding: 0 20px;
		width: 50%;
		font-weight: 500;
		text-align: center;

		span {
			display: block;
			font-weight: 600;
			font-size: 27px;
		}

		p {
			font-size: 13px;
			line-height: 14px;
			color: #483269;
		}

		svg {
			width: 68px;
			height: 68px;
		}
	}

	@include tablet {
		&__star-item {
			margin-bottom: 54px;

			svg {
				margin-bottom: 18px;
				width: 140px;
				height: 140px;
			}

			span {
				margin-bottom: 20px;
				font-size: 55px;
				line-height: 55px;
			}

			p {
				font-size: 24px;
				line-height: 26px;
			}
		}

		&__list-star {
			padding-top: 66px;
		}
	}

	@include desktop {
		&__star-item {
			width: 25%;
		}
	}

	@include desktop-lg {
		padding-top: 54px;

		&__star-item {
			margin-bottom: 98px;

			p {
				padding: 0 35px;
			}
		}
	}
}
