.fly-button {
	width: auto;
	border: 0;
	padding: 6px 28px;
	position: fixed;
	right: 0;
	top: auto;
	bottom: 0;
	color: #fff;
	font-size: 14px;
	z-index: 2;
	box-shadow: -5px 4px 13px 0px rgba(0, 0, 0, 0.75);

	@include tablet {
		top: 131px;
		bottom: auto;
		color: #fff;
		font-size: 30px;
		padding: 11px 51px;
	}
}
